.header {
  background-color: $white;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  border: 1px solid  $c-border;

  &__inner {
    display: flex;
    align-items: center; }

  &__logo {
    margin-right: rem(25); } }

